nav {
    background-color: #000000;
    padding: 10px 15px;
    display: flex;
    justify-content: space-between;
  }
  
  ul {
    list-style-type: none;
    margin: 0;
    padding: 10px;
  }
  
  li {
    display: inline;
    margin-right: 20px;
  }
  
  li:last-child {
    margin-right: 0;
  }
  
  a {
    color:white;
    text-decoration: none;
  }
  
  a:hover {
    color:rgb(247, 62, 93);
  }
  
  /* Page Content */
  .container {
    max-width: 800px;
    margin: 20px auto;
    padding: 0 20px;
  }
  
